import React, { useState } from "react";
import { Link, useNavigate } from "react-router-dom";
import Badge from 'react-bootstrap/Badge';
import Modal from "../Modal";  // Ensure the correct path to Modal
import Cart from "../screen/Cart";
import { useCart } from '../components/ContextReducer';

export default function Navbar() {
  const data = useCart();
  const [cartView, setCartView] = useState(false);
  const navigate = useNavigate();

  const handleLogout = () => {
    localStorage.removeItem("authToken");
    navigate("/login");
  };

  const close = () => {
    setCartView(false);
  };

  return (
    <nav className="navbar navbar-expand-lg navbar-dark bg-dark shadow-sm">
      <div className="container-fluid">
        <Link className="navbar-brand d-flex align-items-center" to="#">
          <h1 className="text-light mb-0">GoFood</h1>
        </Link>
        <button
          className="navbar-toggler"
          type="button"
          data-bs-toggle="collapse"
          data-bs-target="#navbarNavDropdown"
          aria-controls="navbarNavDropdown"
          aria-expanded="false"
          aria-label="Toggle navigation"
        >
          <span className="navbar-toggler-icon"></span>
        </button>
        <div className="collapse navbar-collapse" id="navbarNavDropdown">
          <ul className="navbar-nav me-auto mb-2 mb-lg-0">
            <li className="nav-item">
              <Link className="nav-link text-light" aria-current="page" to="/">
                <h4 className="mb-0">Home</h4>
              </Link>
            </li>
            {localStorage.getItem("authToken") && (
              <li className="nav-item">
                <Link className="nav-link text-light" to="/myOrder">
                  <h4 className="mb-0">My Orders</h4>
                </Link>
              </li>
            )}
          </ul>
          <div className="d-flex align-items-center">
            {!localStorage.getItem("authToken") ? (
              <>
                <Link className="btn btn-success mx-2 text-light" to="/login">
                  <h4 className="mb-0">Login</h4>
                </Link>
                <Link className="btn btn-outline-success mx-2" to="/createuser">
                  <h4 className="mb-0">Sign Up</h4>
                </Link>
              </>
            ) : (
              <>
                <button
                  className="btn btn-outline-light mx-2 d-flex align-items-center"
                  onClick={() => setCartView(true)}
                >
                  <i className="bi bi-cart mx-2"></i>
                  <span className="fs-5">My Cart </span>
                  <Badge pill bg="danger">{data.length}</Badge>
                </button>
                {cartView && (
                  <Modal onClose={close}>
                    <Cart />
                  </Modal>
                )}
                <button
                  className="btn btn-danger mx-2"
                  onClick={handleLogout}
                >
                  <i className="bi bi-box-arrow-right"></i> Logout
                </button>
              </>
            )}
          </div>
        </div>
      </div>
    </nav>
  );
}
