import React, { useEffect, useRef, useState } from "react";
import { useDispatchCart, useCart } from "./ContextReducer";

export default function Card(props) {
  let dispatch = useDispatchCart();
  let options = props.options || {}; // Default to an empty object if props.options is undefined or null
  let priceOptions = Object.keys(options);
  const [qty, setQty] = useState(1);
  const [size, setSize] = useState(priceOptions[0] || ""); // Set default size if options are available
  let data = useCart();
  const priceRef = useRef();

  const handleAddToCart = async () => {
    let foodItem = props.foodItem; // Use props.foodItem directly
    // Find the food item in the cart
    let food = data.find(item => item.id === foodItem._id);

    // If food is found in the cart
    if (food) {
      if (food.size === size) {
        // If the size is the same, update the quantity
        await dispatch({ type: "UPDATE", id: foodItem._id, price: finalPrice, qty });
      } else {
        // If the size is different, add a new entry for the different size
        await dispatch({ type: "ADD", id: foodItem._id, name: foodItem.name, price: finalPrice, qty, size, img: props.ImgSrc });
      }
    } else {
      // If food is not found, add it to the cart
      await dispatch({ type: "ADD", id: foodItem._id, name: foodItem.name, price: finalPrice, qty, size, img: props.ImgSrc });
    }
  };

  let finalPrice = qty * parseInt(options[size] || 0, 10); // Handle cases where size is empty or not available

  useEffect(() => {
    if (priceRef.current) {
      setSize(priceRef.current.value);
    }
  }, [priceOptions]); // Update size if options change

  return (
    <div className="card bg-dark text-light mb-3" style={{ width: "18rem", maxHeight: "350px", margin: "10px" }}>
      <img
        src={props.foodItem.img || "default-image-url"} // Use props.imageSrc or a default value
        className="card-img-top"
        alt="Food"
        style={{ maxHeight: "120px", objectFit: "cover" }}
      />
      <div className="card-body">
        <h5 className="card-title fs-5">{props.foodItem.name}</h5>
        <div className="d-flex flex-column flex-sm-row mb-2">
          <select
            name="quantity"
            className="form-select bg-success text-light me-2"
            onChange={(e) => setQty(e.target.value)}
            value={qty}
          >
            {Array.from(Array(6), (e, i) => (
              <option key={i + 1} value={i + 1}>
                {i + 1}
              </option>
            ))}
          </select>
          <select
            name="priceOption"
            className="form-select bg-success text-light"
            ref={priceRef}
            onChange={(e) => setSize(e.target.value)}
            value={size}
          >
            {priceOptions.map((data) => (
              <option key={data} value={data}>
                {data}
              </option>
            ))}
          </select>
        </div>
        <div className="fs-5 mb-2">{finalPrice}/-</div>
        <button
          className="btn btn-success"
          onClick={handleAddToCart}
        >
          Add to Cart
        </button>
      </div>
    </div>
  );
}
