import React from 'react';
import { useCart, useDispatchCart } from '../components/ContextReducer';

export default function Cart() {
  const data = useCart();
  const dispatch = useDispatchCart();

  if (!Array.isArray(data)) {
    return <div className='alert alert-danger'>Error: Cart data is not an array.</div>;
  }

  if (data.length === 0) {
    return (
      <div className='container text-center my-5'>
        <div className='alert alert-info fs-3'>The Cart is Empty!</div>
      </div>
    );
  }

  const handleRemove = (index) => {
    dispatch({ type: "REMOVE", payload: { id: data[index].id } });
  };

  const handleCheckOut = async () => {
    const userEmail = localStorage.getItem("userEmail");
  
    // Log the values being sent
    console.log("User Email:", userEmail);
    console.log("Order Data:", data);
  
    // Check if userEmail is null or undefined
    if (!userEmail) {
      console.error("User email is missing.");
      return;
    }
  
    const response = await fetch("https://foodaap-backend.onrender.com", {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json'
      },
      body: JSON.stringify({
        order_data: data,
        email: userEmail,
        order_date: new Date().toDateString()
      })
    });
  
    console.log("Order Response:", response);
  
    if (response.ok) {
      dispatch({ type: "DROP" });
    } else {
      const errorData = await response.json();
      console.error("Error:", errorData.message);
    }
  };
  
  

  const totalPrice = data.reduce((total, food) => total + food.price, 0);

  return (
    <div className='container my-5'>
      <div className='table-responsive'>
        <table className='table table-dark table-hover'>
          <thead className='table-secondary'>
            <tr>
              <th scope='col'>#</th>
              <th scope='col'>Name</th>
              <th scope='col'>Quantity</th>
              <th scope='col'>Option</th>
              <th scope='col'>Amount</th>
              <th scope='col'></th>
            </tr>
          </thead>
          <tbody>
            {data.map((food, index) => (
              <tr key={index}>
                <th scope='row'>{index + 1}</th>
                <td>{food.name}</td>
                <td>{food.qty}</td>
                <td>{food.size}</td>
                <td>{food.price}</td>
                <td>
                  <button
                    type="button"
                    className="btn btn-dark btn-sm"
                    onClick={() => handleRemove(index)}
                  >
                    
                  </button>
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
      <div className='d-flex justify-content-between align-items-center'>
        <h2 className='text-light'>Total Price: {totalPrice} /-</h2>
        <button className='btn btn-success' onClick={handleCheckOut}>
          Check Out
        </button>
      </div>
    </div>
  );
}
